@charset "UTF-8";
/*标题，抬头css*/
.webmain .con {
  margin-bottom: 45px; }

.webmain .con .title {
  height: 60px;
  overflow: hidden;
  background: url("../../../images/line_contitle.gif") center center repeat-x;
  margin-bottom: 15px;
  position: relative; }

.webmain .con .title a.more {
  display: block;
  position: absolute;
  right: 0;
  top: 16px;
  background: url("../../../images/more_contitle.gif");
  width: 130px;
  height: 30px;
  text-indent: -99em;
  overflow: hidden; }

.webmain .con .title h2 {
  display: block;
  width: 200px;
  height: 60px;
  background-image: url("../../../images/p_starinfo_1.jpg");
  background-repeat: no-repeat;
  text-indent: -99em;
  overflow: hidden;
  float: left; }

.webmain .con .title ul {
  float: left;
  height: 26px;
  background: #fff;
  padding: 17px 5px 17px 5px; }

.webmain .con .title ul li {
  float: left;
  font-size: 16px;
  height: 26px;
  line-height: 26px;
  overflow: hidden;
  margin-right: 8px; }

.webmain .con .title ul li a {
  display: block;
  width: 100%;
  height: 26px;
  line-height: 26px;
  cursor: pointer;
  padding: 0 8px; }

.webmain .con .title ul li.c a {
  background: #000;
  color: #fff; }

.share {
  position: absolute;
  margin-left: 510px;
  margin-top: -50px; }
  .share a {
    display: inline-block;
    width: 120px;
    height: 20px;
    text-indent: 30px;
    font-size: 16px;
    background: url("../../../images/sprite_weiboweixin.gif") 0 0 no-repeat; }
  .share .weibo {
    background-position: 0 -19px; }
    .share .weibo:hover {
      background-position: 0 -0; }
  .share .weixin {
    background-position: 0 -60px; }
    .share .weixin:hover {
      background-position: 0 -39px; }
  .share .weixinshare {
    display: none;
    width: 215px;
    height: 243px;
    margin-left: 65px;
    text-align: center;
    background: url("../../../images/p_weixincodeframe.png") 0 0 no-repeat; }
    .share .weixinshare h3, .share .weixinshare p, .share .weixinshare img {
      display: inline-block; }
    .share .weixinshare h3 {
      height: 40px;
      margin-top: 10px;
      font-size: 12px;
      line-height: 40px; }
    .share .weixinshare p {
      color: #ccc;
      font-size: 16px;
      line-height: 25px; }
    .share .weixinshare img {
      width: 125px;
      height: 125px;
      margin-top: 10px; }

.introduction-face, .introduction-face img {
  width: 220px;
  height: 320px; }

.introduction-face {
  float: left; }

.introduction-content {
  margin-left: 455px; }
  .introduction-content .name {
    font-size: 40px; }
  .introduction-content .details {
    font-size: 14px;
    line-height: 30px;
    margin-top: 20px; }
    .introduction-content .details .left-col {
      float: left; }
    .introduction-content .details .right-col {
      margin-left: 330px; }

.des {
  display: inline-block;
  width: 80px;
  color: #ccc; }

.worklist-main .imglist {
  width: 550px;
  height: 770px; }

.worklist-main .tabsection li, .worklist-main .tabsection li img {
  width: 95px;
  height: 133px; }

.worklist-main .imglist {
  margin: 45px auto; }
  .worklist-main .imglist li {
    display: none;
    text-align: center; }
    .worklist-main .imglist li.active {
      display: block; }
    .worklist-main .imglist li img {
      position: absolute;
      width: 500px;
      height: 700px;
      margin-left: 25px; }
    .worklist-main .imglist li .workname {
      position: relative;
      display: inline-block;
      margin-bottom: 45px;
      font-size: 14px; }
.worklist-main .tabsection {
  position: absolute;
  margin-top: -780px;
  margin-left: 1105px; }
  .worklist-main .tabsection .up {
    position: absolute;
    width: 95px;
    height: 27px;
    margin-top: -37px;
    background: url("../../../images/sprite_starinfo_toggle.png"); }
  .worklist-main .tabsection .down {
    display: inline-block;
    width: 95px;
    height: 27px;
    background: url("../../../images/sprite_starinfo_toggle.png") 0 -27px no-repeat; }
  .worklist-main .tabsection .frame {
    width: 89px;
    height: 127px;
    position: absolute;
    top: 0;
    border: 3px solid #9966cc;
    transition: all 0.3s linear; }
  .worklist-main .tabsection li {
    margin-bottom: 10px; }
